@import "/assets/main.css";

.float-left {
    float: left;
    margin-right: 1em;
    margin-bottom: 1em;
    max-width: 40%;
}

.float-right {
    float: right;
    margin-left: 1em;
    margin-bottom: 1em;
    max-width: 40%;
}

.featured-video {
    display: block;
    max-width: 50%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2em;
    margin-bottom: 2em;
}

.featured-video-horizontal {
	@extend .featured-video;
	max-width: 80%;
}

/* Narrow screens (e.g. mobile) */
@media (max-width: 767px) {
  .featured-video {
    max-width: 80%;
  }
}

.img-headshot {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 150px;
}
